@import "./button";
@import "./input";
@import "./game";
@import "./console";
@import "./loader";

$background-gradient: linear-gradient(to bottom, #aaffa9, #11ffbd);

body, html {
  margin: 0;
}

body {
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
}

.game {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $background-gradient;
  flex-direction: column;
  font-size: 18px;
}


.title {
  font-family: 'Bungee Shade';
  text-align: center;
  font-size: 2em;
  margin-bottom: 1em;
}

.photo-canvas, .snake-canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
}

.photo-canvas {
  left: -200%
}

.snake-canvas {
  display: block;
}

$bg: #74ffb0;
#gifContainer {
  height: 300px;
  position: relative;
  align-items: center;
  justify-content: center;
  display: flex;
  
  .text {
    font-family: 'Rubik', sans-serif;
    position: absolute;
    width: 300px;
    top: -35px;
    lefT: -100%;
    text-align: center;
  }
  
  
  img {
    position: relative;
  }
  
  img + .share {
    content: 'Share';
    color: white;
    position: absolute;
    display: block;
    bottom: 10px;
    right: 10px;
    /*
    &:before {
      background-image: url(https://image.pngaaa.com/850/1385850-small.png);
      display: inline-block;
      content: '';
      width: 10px;
      height: 10px;
    }
    */
    
  }
}

.title-gameover {
  margin-top: -60px;
  margin-bottom: 10px;
}

.replay {
  margin-top: 20px;
  font-family: 'Rubik', sans-serif;
}


.replay {
  
  $top-pad: 6px;
  $side-pad: 15px;
  $border-size: 2px;
  
  position: relative;
  display: inline-block;
  text-decoration: none;
  padding: 6px 15px;
  font-size: 1.2em;
  background: none;
  color: black;
  border: 2px solid black;
  box-shadow: 0 0 0 $border-size #74ffb0, 0 0 0 ($border-size *2) black;
  transition: all 0.2s ease-in-out;
  will-change: box-shadow, margin, padding;
  cursor: pointer;
  
}
